
import "../css/AboutMe.css";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import mgram from "../picture/mgram.jpg";

export const AboutMe = () => {
  return(
    <section id="about-me">
      <div className="about-me">
        <div className="title-container">
          <h2>About Me</h2>
          <p> </p>
        </div>
        <div className="personal-card-container">
          <Card sx={{ width: 800,
                      minHeight: 800,
                      marginRight: 10,
                      backgroundColor: "#f1e5d0",
                      boxShadow:10, }}>
            <CardContent>
              <Typography sx={{ fontSize: 45,
                                padding: 5,
                                borderBottom: 1}}>
                <br/>
              </Typography>
              <Typography sx={{ fontSize: 34,
                                padding:7,
                                textAlign: "left"}}
                                color="text.secondary"
              >
                
              </Typography>
            </CardContent>
          </Card>
          <Card sx={{ width: 800,
                      minHeight: 800,
                      backgroundColor: "#f1e5d0",
                      boxShadow:10,}}
          >
            <CardContent>
              <Typography sx={{ fontSize: 45,
                                padding: 5,
                                borderBottom: 1}}
              >
                <br/>
              </Typography>
              <Typography sx={{ fontSize: 34,
                                padding:7,
                                textAlign: "left"}}  color="text.secondary"
              >
                1
              </Typography>
            </CardContent>
          </Card>
        </div>
        <div className="mgram-image">
          <img src={mgram} alt="mgram" />
          <a href="https://mgram.me/ja/user/profiling/edit">mgram</a>
        </div>
      </div>
    </section>
  );
};